.App {
  margin: 0;
  padding: 0;
  height: 100%;
  background-color: #2490d5;
  background-image: linear-gradient(
    135deg,
    #2490d5 0%,
    #6e73cd 50%,
    #222d5f 100%
  );
}

body > #root > div {
  height: 100%;
}

.navbar-brand {
  float: none;
  font-size: 24px !important;
  font-weight: bold;
}
.cards {
  text-align: center;
  margin: 10px;
  margin-bottom: 2px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.summaryCards {
  text-align: center;
  margin: 10px;
  padding: 10px;
}
.summaryText {
  font-weight: bold;
  font-size: 42px !important;
}
.flag {
  padding: 2px;
  align-self: center;
  height: 200px;
  width: 100%;
}

.centered {
  width: 100vw;
  text-align: center;
}

.footer {
  color: white;
  text-align: center !important;
}

.card-text {
  font-size: medium;
  margin-bottom: -2px;
}

.card-title {
  margin-top: -15px;
  font-weight: bolder !important;
}

.form-control {
  margin: auto;
  width: 50vw !important;
}

.buffer {
  padding: 80px;
}
